* {
  box-sizing: border-box;
}

html {
  height: 100%;
}
body {
  min-height: 100%;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
text-decoration: none;
cursor: pointer;
}

a:hover {
text-decoration: none;
cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  width: auto;
}

form {
  width: 100%;
}

.overflowHidden {
  overflow: hidden;
}
#portal {
  position: fixed;
  z-index: 50;
}
#root,
#root > div {
  min-height: 100vh;
  height: 100%;
  position: relative;
  z-index: 50;
}

.alert-enter {
  opacity: 0;
  margin: 30px;
  left: 60px;
  transform: scale(1);
}
.alert-enter-active {
  opacity: 1;
  left: 0px !important;
  transform: translateX(0);
  transition: opacity 300ms, transform 3000ms, left 300ms;
}
.alert-exit {
  left: 0px;
  opacity: 1;
}
.alert-exit-active {
  opacity: 0;
  transform: scale(0.9) translateY(9);
  left: -60px;
  transition: opacity 300ms, transform 3000ms, left 300ms;
}

/* ------------ */
/* Fade In Left     */
/* ------------ */
.fadeIn-enter {
  opacity: 0;
  transform: scale(1);
}
.fadeIn-enter-active {
  opacity: 1;
  transition: opacity 300ms, transform 3000ms, right 300ms;
}
.fadeIn-exit {
  opacity: 1;
}
.fadeIn-exit-active {
  opacity: 0;
  transition: opacity 300ms, transform 3000ms, right 300ms;
}

/* ------------ */
/* Fade In Left     */
/* ------------ */
.fadeInLeft-enter {
  opacity: 0;
  right: -100px !important;
  transform: scale(1);
}
.fadeInLeft-enter-active {
  opacity: 1;
  right: 0px !important;
  transition: opacity 300ms, transform 3000ms, right 300ms;
}
.fadeInLeft-exit {
  opacity: 1;
  right: 0px !important;
}
.fadeInLeft-exit-active {
  opacity: 0;
  right: -100px !important;
  transition: opacity 300ms, transform 3000ms, right 300ms;
}


